@import "./mixins/vendor-prefixes.less";
@import "variables.less";

/* mix-in definition*/
.highlight-border-on-hover {
  background: white;
  border-color: @input-border-hover;
  box-shadow: @n-search-highlight-border;
}

wf-search .n-search-input {
  &::-webkit-input-placeholder {
    color: @gray-medium;
  }
  &::-moz-placeholder {
    color: @gray-medium;
  }
  &:-ms-input-placeholder {
    color: @gray-medium;
  }
}

.n-search {
  position: relative;
  width: @n-search-width;

  &-icon {
    cursor: default;
    left: 0;
    padding-left: 11px;
    position: absolute;
    top: 0;
  }

  &-control-icon,
  &-button {
    border-radius: @n-search-border-right-round;
    cursor: pointer;
    height: @n-search-height;
    margin: 0;
    padding: 0 10px 0 9px;
    position: absolute;
    right: 0;
    top: 0;
  }



  &-button {
    box-shadow: none;

    &:focus {
      box-shadow: none;
    }
    &:hover:not(:focus) {
      box-shadow: @n-search-highlight-border;
    }
  }

  &-input {
    border-radius: @n-search-input-corner-radius;
    height: @n-search-height;
    padding: 0 10px 0 38px;
    width: inherit;
  }

  .dropdown-toggle {
    background: transparent;
    border: 1px solid transparent;
    border-radius: @n-search-border-left-round;
    box-shadow: none;
    height: @n-search-height;
    cursor: pointer;

    &:hover{
      border: 1px solid @input-border-hover;

      .icon:before{
        color: @gray-medium;
      }
    }

    &:focus:not(:hover){
      background-image:none !important;
      border: 1px solid @focus-border-style;
    }

    &[aria-expanded=true] {
      background: transparent !important;
      border: 1px solid transparent !important;
      border-radius: @n-search-border-left-round !important; //TODO: because there is !important in .dropdown-toggle[aria-expanded=true]. unwanted, must get rid of
    }
  }

  .dropdown-menu {
    background-color: @dropdown-menu-background;
    width: inherit;
    * {
      background-color: transparent;
    }
  }

  .icon:not(.icon-close) {
    font-size: 12px;
    line-height: @n-search-height;
    .user-select(none);
    & + .icon {
      margin-left: -3px;
    }
  }

  &.open {
    .n-search-input {
      border-radius: @n-search-border-up-round;
    }

    .n-search-control-icon,
    .n-search-button {
      border-radius: @n-search-border-up-right-round;
    }

    .dropdown-toggle {
      border-radius: @n-search-border-up-left-round !important;
      &:hover {
        background-color: transparent;
        border-color: transparent;
        box-shadow: none;
      }
    }
  }

  a:focus {
    outline:none;
  }
}

.n-search-action .n-search-input
{
    padding-right: 67px;
}

.n-search-clearable .n-search-input {
  padding-right: 35px;
}

.n-search-clearable {
  .n-search-control-icon {
    line-height: 33px;
    .icon-close {
      &:before {
        color: @gray-medium;
      }

      border: 1px solid transparent;
      border-radius: 5px;
    }
    &:focus {
      border: 1px solid @focus-border-style;
      .icon-close {
        top:-1px;
        left:1px;
      }
    }
    &:hover  {
          border: 1px solid @n-search-clearable-icon-hover-border;
          box-shadow: inset 0 0 0 1px @n-search-clearable-icon-hover-shadow;
          .icon-close {
            top:-1px;
            left:1px;
            &:before{
              color: @gray-medium;
            }
          }
    }
  }
}

.n-search-disable {
  .n-search-icon {
    position: absolute;
    top: 0;
    left: 0;
    padding-left: 11px;
    cursor: default;

    .icon-arrow:before {
      color: @n-search-disable-icon-arrow !important;
    }

    .icon-search:before {
      color: @n-search-disable-icon-search !important;
    }
  }

  .n-search-control-icon {
    line-height: 33px;
    .icon-close {
      border: 1px solid transparent;
      border-radius: 5px;
      &:before {
        color: @n-search-control-icon-close !important;
      }
    }
    &:focus > .icon-close {
      border-color: @n-search-control-icon-focus-close-border;
    }
    &:hover > .icon-close {
      border-color: @n-search-control-icon-hover-close-border;
    }
    &[disabled] {
      border-bottom: 1px solid @input-border-disabled;
    }
  }
}
